.title {
  font-size: 1.5em;
  font-weight: 600;
  padding: 10px;
}
.pagination {
  text-align: center;
  max-width: 100%;
  margin: 0 5px;
}
.pagination span {
  font-size: 1em;
  padding: 3px 5px;
  cursor: pointer;
  display: inline-block;
}
.selectedPage {
  background-color: #5cdb95;
  color: #fff;
  font-weight: 700;
  border-radius: 4px;
}
.buttonGet {
  background-color: #5cdb95;
  border: none;
  border-radius: 5px;
  color: #fff;
  text-shadow: 0 0 2px #05386b;
  padding: 5px 30px;
  align-self: flex-end;
  cursor: pointer;
  font-size: 1em;
  font-weight: 600;
}
.wrap {
  display: grid;
  grid-template-columns: 2fr 10fr;
  grid-gap: 10px;
  margin: 3rem 0;
}
.wrapUser {
  display: grid;
  justify-items: center;
}
.avatar {
  width: 100px;
}
.wrapButton {
  margin-top: 10px;
}
.wrapButton Button {
  background-color: #5cdb95;
  border: none;
  border-radius: 5px;
  color: #fff;
  text-shadow: 0 0 2px #05386b;
  padding: 5px;
  width: 100px;
  align-self: flex-end;
  cursor: pointer;
  font-size: 0.9em;
  font-weight: 600;
}
.wrapDescription {
  display: grid;
  grid-template-columns: 8fr 2fr;
  border: 2px solid #5cdb95;
  border-radius: 20px;
  padding: 20px;
  margin-right: 50px;
}
.fullName {
  font-size: 1.2em;
}
.statusUser {
  font-size: 1.2rem;
  font-weight: 500;
  align-self: end;
}
.alignment {
  display: grid;
}
.country {
  font-weight: 500;
}
.city {
  font-size: 0.9em;
}
